<script>
    import Alert from '../Alert/Alert.svelte'
    import {Leads} from '../../services'
    let name = "";
    let phone = "";
    let email = "";
    let description = "";

    let message = "";
    let submiting = false;
    let resp 

    async function submitForm() {
        submiting = true;
        const res = await Leads.save({name, email, phone, description})

        if(res.error){
            resp = {
                status: "error",
                message: "Error occured while submitting, please try again later!"
            }
        }else{
            resp = {
                status: "success",
                message:"Thank you for contacting us, we will be in touch with you shorly."
            }
        }
    }
</script>

<style src="./connect.css">section {
    margin: 4rem 0;
    min-height: 37.5rem;
}
h1 {
    text-align: center;
    font-size: 2.6rem;
    color: var(--light-gray-color);
    font-weight: lighter;
}
.container {
    max-width: 50rem;
    padding: 2rem;
    margin: 0 auto;
    color: var(--light-gray-color);
}
.container .center {
    text-align: center;
}
form {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}
label {
    font-size: 1rem;
    font-weight: 600;
    text-transform: capitalize;
    color: var(--gray-color);
    letter-spacing: 1px;
}
input,
textarea {
    margin-top: 0.3rem;
    box-sizing: border-box;
    width: 100%;
    background: var(--input-bg-color);
    padding: 0.5rem;
    border: none;
    outline: none;
    font-size: 1rem;
    resize: none;
    color: var(--third-color);
    border-radius: 2px;
}
textarea {
    height: 9.375rem;
    font-family: sans-serif;
}
.connect {
    padding: 0.5rem;
    font-size: 1rem;
    background: var(--first-color);
    color: var(--third-tr-color);
    font-weight: bold;
    border: none;
    outline: none;
}
.connect:hover {
    background: var(--bg-hover-color);
}
.connect:disabled {
    background: var(--first-color);
    opacity: 0.6;
}
input::placeholder,
textarea::placeholder {
    color: var(--placeholder-color);
}
button {
    cursor: pointer;
    user-select: none;
    text-transform: uppercase;
}
.disabled{
    cursor: not-allowed;
    opacity: 0.5;
}
@media (max-width: 480px) {
    h1 {
        font-size: 10vw;
    }
}</style>

<section id="connect">
    <h1>Get in Touch</h1>
    <div class="container">
        {#if resp}
            <div class="center">
                <Alert {...resp} />
            </div>
        {:else}
            <form on:submit|preventDefault={submitForm}>
                <label for="name">
                    name:
                    <input
                        type="text"
                        class="name"
                        placeholder="Full Name"
                        required
                        spellcheck="false"
                        bind:value={name} />
                </label>
                <label for="phone">
                    phone:
                    <input
                        type="tel"
                        class="phone"
                        placeholder="Phone Number"
                        pattern="\+?[-0-9 ]{'{3,15}'}"
                        bind:value={phone} />
                </label>
                <label for="email">
                    email:
                    <input
                        type="email"
                        class="email"
                        placeholder="Email Address"
                        required
                        bind:value={email} />
                </label>
                <label for="description">
                    description:
                    <textarea
                        class="description"
                        placeholder="type your message here"
                        required
                        spellcheck="false"
                        bind:value={description} />
                </label>
                <button
                    class="connect {submiting ? 'disabled':''}"
                    disabled={submiting}>{message || 'CONNECT'}</button>
            </form>
        {/if}
    </div>
</section>
